export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['componentPure'] = "class MyComponent extends Rete.Component {\n  constructor() {\n    super(\"My Component\"); // name\n  }\n\n  builder(node) {\n    /// modify node\n  }\n\n  worker(node, inputs, outputs) {\n    /// process data\n  }\n}";
    }