export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['worker'] = "class NumberComponent extends Rete.Component {\n  constructor(){\n    super('Number');\n  }\n\n  async worker(node, inputs, outputs){\n    // inputs['key1']\n    outputs['key2'] = node.data.num;\n  }\n}";
    }